let itemList = [
    {icon: "el-icon-setting", label:"", route:"/setting"},
    //{icon: "el-icon-money", label: "", route: "/salary-calculator", backgroundColor: "#afefbf", iconColor: "#44aa56"},
    //{icon: "el-icon-notebook-2", label: "", route: "/todo-list", backgroundColor: "#ffee70", iconColor: "#bc7e21"},
    {icon: "el-icon-brush", label: "", route: "/color-converter", backgroundColor: "#ffffef", iconColor: "#66665f"},
    {icon: "el-icon-map-location", label: "", route: "/map", backgroundColor: "#fefffd", iconColor: "#6699ff"},
    {icon: "el-icon-bell", label: "", route: "/test", backgroundColor: "#fefffd", iconColor: "#6699ff"},
    //{icon: "el-icon-coin", label: "", route: "/color-converter", backgroundColor: "#ffffef", iconColor: "#66665f"},
];

export default {
    namespaced: true,
    state: {
        config: {
            pages: 2,
        },
        items: itemList,
        map: (():Array<number> => {
            let mapString = localStorage.getItem("DesktopMap");
            if(mapString !== null) {
                // 
                let map: Array<any> = mapString.split(";");
                map.forEach((e,i) => {
                   map[i] = parseInt(e);
                });
                return map;
            } else {
                // 
                let t: Array<number> = [],len = itemList.length;
                for(let i = 0; i < len; i++ ) {
                    t[i] = i;
                }
                localStorage.setItem("DesktopMap",t.join(";"));
                return t;
            }
        })()

    },
    mutations: {
        items(state: any, items: Array<Object>):void {
            state.items = items;
        },
        map(state: any, map: Array<number>): void {
            state.map = map;
            localStorage.setItem("DesktopMap",map.join(";"))
        }
    },
    actions: {
        items(context: any, items: Array<Object>): void {
            context.commit("items", items);
        },
        map(context: any, map: Array<number>): void {
            context.commit("map", map);
            console.log("store");
        }
    }
}
